"use strict";
function sortGenericByDiff(diff, order) {
    if (order === 'asc') {
        return diff;
    }
    else {
        return diff * -1;
    }
}
function sortGamesByStart(a, b, order = 'asc') {
    const diff = a.start.getTime() - b.start.getTime();
    return sortGenericByDiff(diff, order);
}
exports.sortGamesByStart = sortGamesByStart;
function fastConcat(array, otherArray) {
    otherArray.forEach((v) => array.push(v));
}
exports.fastConcat = fastConcat;



